/* Timeline-2 */
.timeline-2 {
  position: relative;
  border-left: 2px solid #36404c; }
  .timeline-2 .time-item {
    position: relative;
    padding-bottom: 1px;
    border-color: #f3f3f3; }
    .timeline-2 .time-item :before, .timeline-2 .time-item :after {
      display: table;
      content: " "; }
    .timeline-2 .time-item :after {
      position: absolute;
      left: 0;
      top: 5px;
      width: 14px;
      height: 14px;
      margin-left: -8px;
      background-color: #fff;
      border-color: #36404c;
      border-style: solid;
      border-width: 2px;
      border-radius: 10px;
      content: ''; }

